.App {
    .search-field {
        width: 100%;
        height: 100vh;
        position: fixed;
        overflow: scroll;
        display: flex;
        justify-content: center;
        top: 0;
        left: 0;
        z-index: 10;
        background-color: #B4B4B4
    }

    .wrapper {
        @include flexCenter();
        width: 90%;
        height: fit-content;
        flex-direction: column;
        overflow: scroll;

        .search-wrapper {
            width: 100%;
            position: relative;
        }
    }


}

@import './SearchResult';